import React from 'react';

// libs
import Axios from 'axios';

// VK
import connect from '@vkontakte/vk-connect';
import connectPromise from "@vkontakte/vkui-connect-promise";
import { 
	Avatar,
	Button,
	Cell,
	ConfigProvider,
	Div,
	Epic,
	Group,
	List,
	Panel,
	PanelHeader,
	PullToRefresh,
	Spinner,
	Tabbar,
	TabbarItem,
	UsersStack,
	View
} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
// import { IS_PLATFORM_ANDROID, IS_PLATFORM_IOS } from '@vkontakte/vkui/dist/lib/platform';


import Icon16Like from '@vkontakte/icons/dist/16/like';
import Icon28Newsfeed from '@vkontakte/icons/dist/28/newsfeed';
import Icon28User from '@vkontakte/icons/dist/28/profile';
import Icon28Users from '@vkontakte/icons/dist/28/users';
//import Map from './components/Map';

// styles
import './styles/App.css';

const ENABLE_DEBUG = true;

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			// app
			activePanel: 'discover',
			activeStory: 'discover',
			activeView: 'discover',
			appInitTimestamp: 0,
			fetching: false,

			// user
			fetchedUser: null,
			isAuthorized: false,

			userAllowedScopes: false, // TODO

			userData: [],
			userDiscoverFeed: [],
			userFriends: [],
			userFriendsInApp: [],
			userGeodata: null,
			userGroups: [],
			vkAccessToken: null,
		};

		this.onPanelChange = this.onPanelChange.bind(this);
		this.onStoryChange = this.onStoryChange.bind(this);
		this.onViewChange = this.onViewChange.bind(this);

		this.onRefresh = this.onRefresh.bind(this);
	}

	componentDidMount() {
		//console.log(this.state);

		this.setState({
			appInitTimestamp: this.getTimestamp(),
		});

		// VK CONNECT
		connect.subscribe((e) => {
			if (e.detail.hasOwnProperty('type')) {
				switch (e.detail.type) {
					case 'VKWebAppGetUserInfoResult':
						// save user data
						this.setState({ fetchedUser: e.detail.data });
	
						// auth user
						Axios.get('https://ybsapi.herokuapp.com/user/authorize/' + e.detail.data.id)
							.then(response => {
								console.log('! YBS-API: user/authorize');

								if (response.data.hasOwnProperty('data')) {
									this.setState({
										isAuthorized: true
									});

									if (ENABLE_DEBUG) {
										console.log(response.data.data);
									}
								} else {
									// TODO: error message
									if (ENABLE_DEBUG) {
										console.log('! Auth: Failed!');
									}
								}
							})
							.catch(error => {
								if (ENABLE_DEBUG) {
									console.log('ERROR: ' + error);
									//console.error(error);
								}
							});
						break;
					case 'VKWebAppGeodataResult':
						// console.log('! Result: Geodata');
						// console.log(e.detail.data.lat + ',' + e.detail.data.long);

						this.setState({ 
							userGeodata: {
								lat: e.detail.data.lat,
								lng: e.detail.data.long
							}
						});
						break;
					case 'VKWebAppAccessTokenReceived':
						if (ENABLE_DEBUG) {
							console.log('! Result: AccessToken');
						}

						if (e.detail.data.hasOwnProperty('access_token')) {
							this.setState({
								vkAccessToken: e.detail.data.access_token
							});
	
							this.getFriends();
							this.getGroups();
						} else {
							if (ENABLE_DEBUG) {
								console.log('ACCESS_TOKEN_ERROR');
							}
						}
						
						break;
					case 'VKWebAppCallAPIMethodResult':
						if (e.detail.data.hasOwnProperty('request_id')) {
							switch (e.detail.data.request_id) {
								case '1fr':
									// TODO: УДАЛИТЬ НАХУЙ
									const SEND_FRIENDS_GROUPS = false;
									if (SEND_FRIENDS_GROUPS) {
										let few_friends = e.detail.data.response.items; //e.detail.data.response.items.slice(10, 20);

										console.log('! FEW');

										few_friends.forEach(value => {
											console.log('! friend user_id: ' + value.id);

											console.log(this.callAPI('groups.get', {
												'user_id': value.id,
												'fields': 'city,country,place,description,members_count,links,site,is_favorite,photo_100,photo_200',
												'count': 50,
												'extended': true,
											}).then(function(response) {
												Axios.post('https://ybsapi.herokuapp.com/user/communities/' + value.id, response.items)
													.then(resp => {
														
													})
													.catch(error => {
														if (ENABLE_DEBUG) {
															console.log('ERROR: ' + error);
														}
													});
											}));
										});
									}

									// send friends
									Axios.post('https://ybsapi.herokuapp.com/user/friends/' + this.state.fetchedUser.id, e.detail.data.response.items)
										.then(response => {
											console.log('! YBS-API: user/friends');
											
											if (response.data.hasOwnProperty('data')) {
												if (ENABLE_DEBUG) {
													console.log(response.data.data);
												}

												this.setState({
													userFriendsInApp: response.data.data,
												});
											}
										})
										.catch(error => {
											if (ENABLE_DEBUG) {
												console.log('ERROR: ' + error);
											}
										});

									this.setState({ 
										userFriends: e.detail.data.response.items 
									});
									break;

								case '1gr':
									// send groups
									Axios.post('https://ybsapi.herokuapp.com/user/communities/' + this.state.fetchedUser.id, e.detail.data.response.items)
										.then(response => {
											if (ENABLE_DEBUG) {
												console.log('! YBS-API: user/communities');
											}
											
											if (response.data.hasOwnProperty('data')) {
												if (ENABLE_DEBUG) {
													console.log(response.data.data);
												}

												this.setState({
													userDiscoverFeed: response.data.data,
												});
											}
										})
										.catch(error => {
											if (ENABLE_DEBUG) {
												console.log('ERROR: ' + error);
											}
										});

									this.setState({ 
										userGroups: e.detail.data.response.items 
									});
									break;
							}
						}
						break;
					default:
						// debug
						if (ENABLE_DEBUG) {
							console.warn(e.detail.type);
						}
				}
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
		//connect.send('VKWebAppGetGeodata', {});

		this.getToken();
	}

	callAPI = (method, params) => {
		params['access_token'] = this.state.vkAccessToken;
		params['v'] = '5.101';
	
		return connectPromise.send('VKWebAppCallAPIMethod', {
			'method': method,
			'params': params
		}).then(data => {
			return data.data.response;
		}).catch(error => {
			return error;
		});
	};

	getAppRuntime() {
		return (this.getTimestamp() - this.state.appInitTimestamp);
	}

	getTimestamp() {
		return Math.floor(Date.now() / 1000);
	}

	getFriends() {
		connect.send('VKWebAppCallAPIMethod', {
			'method': 'friends.get',
			'request_id': '1fr',
			'params': {
				'fields': 'city,country,domain,first_name,last_name,photo_100,photo_200',
				'count': 1000,
				'order': 'mobile',
				'access_token': this.state.vkAccessToken,
				'v': '5.101',
			}
		});
	}

	getGroups = () => {
		connect.send('VKWebAppCallAPIMethod', {
			'method': 'groups.get',
			'request_id': '1gr',
			'params': {
				'fields': 'city,country,place,description,members_count,links,site,is_favorite,photo_100,photo_200',
				'count': 1000,
				'extended': true,
				'access_token': this.state.vkAccessToken,
				'v': '5.101',
			}
		});
	}

	getToken = () => {
		connect.send('VKWebAppGetAuthToken', {'app_id': 7150409, 'scope': 'friends,groups'});
	}

	onPanelChange (e) {
		this.setState({
			activePanel: e.currentTarget.dataset.panel
		});
	}

	onRefresh = () => {
		this.setState({ fetching: true });
  
		setTimeout(() => {
			this.setState({
				fetching: false
			});
		});
	}

	onStoryChange (e) {
		this.setState({ activeStory: e.currentTarget.dataset.story });
	}

	onViewChange (e) {
		this.setState({ activeView: e.currentTarget.dataset.view });
	}

	render() {
		return (
			<ConfigProvider isWebView={true}>
				<Epic 
					activeStory={this.state.activeStory} 
					tabbar={
						<Tabbar>
							<TabbarItem
								onClick={this.onStoryChange}
								selected={this.state.activeStory === 'discover'}
								data-story="discover"
								text="Актуальное"
							><Icon28Newsfeed /></TabbarItem>
							<TabbarItem
								onClick={this.onStoryChange}
								selected={this.state.activeStory === 'friends'}
								data-story="friends"
								text="Друзья"
							><Icon28Users /></TabbarItem>
							<TabbarItem
								onClick={this.onStoryChange}
								selected={this.state.activeStory === 'profile'}
								data-story="profile"
								text="Профиль"
							><Icon28User /></TabbarItem>
						</Tabbar>
					}
				>
						<View id="discover" activePanel="discover">
							<Panel id="discover">
								<PanelHeader>
									Актуальное
								</PanelHeader>
								<PullToRefresh onRefresh={this.onRefresh}>
									{(this.state.userDiscoverFeed.length === 0 && this.getAppRuntime() <= 10) && 
										<Group style={{paddingTop: '20px'}}>
											<Spinner size="medium" style={{ marginTop: 20 }} />
											<Div style={{textAlign: 'center'}}>
												Мы подбираем интересные места для Вас, это может занять какое-то время
											</Div>
										</Group>
									}

									{(this.state.userDiscoverFeed.length === 0 && this.getAppRuntime() > 10) && 
										<Group style={{paddingTop: '20px'}}>
											<Div style={{textAlign: 'center'}}>
												Упс, что-то пошло не так (
											</Div>
										</Group>
									}

									<Group>
										<List>
											{this.state.userDiscoverFeed.map((item, i) => 
												// <Group key={i}>
													<Div key={i}>
														<Cell before={<Avatar src={item.icon} />}description={ item.description }>
															{ item.name }
														</Cell>
														{/* <Button level="outline" style={{marginLeft: '10px'}}>
															<Icon16Like fill="var(--blue)" />
														</Button> */}
													</Div>
													
												// </Group>
											)}
										</List>
									</Group>
								</PullToRefresh>
							</Panel>
						</View>

						<View id="friends" activePanel="friends">
							<Panel id="friends">
								<PanelHeader>
									Друзья
								</PanelHeader>
								<Group>
									<List>
										{this.state.userFriendsInApp.map((item, i) => 
											<Div key={i}>
												<Cell before={<Avatar src={ item.photo_200 } />}>
													<span style={{
														color: '#3a3a3a',
													}}>
														{ item.first_name + ' ' + item.last_name }
													</span>
													<br />
													<span style={{color: '#9a9a9a'}}>
														1 уровень
													</span>
												</Cell>
											</Div>
										)}
									</List>
								</Group>
							</Panel>
						</View>

						<View id="profile" activePanel="profile">
							<Panel id="profile">
								<PanelHeader>
									Профиль
								</PanelHeader>
								{this.state.fetchedUser &&
									<Group>
										<List>
											<Div>
												<Cell
													before={this.state.fetchedUser.photo_200 ? <Avatar src={this.state.fetchedUser.photo_200}/> : <Avatar><Icon28User /></Avatar>}
												>
													<span style={{
														color: '#3a3a3a',
													}}>
														{`${this.state.fetchedUser.first_name} ${this.state.fetchedUser.last_name}`}
													</span>
													<br />
													<span style={{color: '#9a9a9a'}}>
														1 уровень
													</span>

												</Cell>

												
											</Div>
										</List>
									</Group>
								}
							</Panel>
						</View>
				</Epic>
			</ConfigProvider>
		);
	}
}

export default App;
